import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams, ViewController} from 'ionic-angular';
import {AppConfigProvider} from '../../providers/appconfig/appconfig';

/**
 * Generated class for the PersonCreatorPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-person-creator',
  templateUrl: 'person-creator.html',
})
export class PersonCreatorPage {

  user: any = {};
  person: any;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              private acp: AppConfigProvider,
              private viewCtrl: ViewController,
              private alertCtrl: AlertController) {
    this.person = this.navParams.get('person');
    this.user = this.acp.getUser();
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PersonCreatorPage');
  }

  close(): void {
    this.viewCtrl.dismiss();
  }

  saveChanges(): void {
    this.viewCtrl.dismiss(this.person);
  }

  delete(): void {
    const alert = this.alertCtrl.create({
      title: 'Delete message?',
      message: 'Er du helt sikker?',
      buttons: [
        {
          text: 'Behold',
          handler: () => {
            return;
          }
        },
        {
          text: 'Slett',
          handler: () => {
            this.person.delete = true;
            this.viewCtrl.dismiss(this.person);
            return;
          }
        }
      ]
    });
    alert.present();
  }

}



